.top-bar{
	background-color:$black;
	padding: 10px 15px;
    vertical-align: middle;
    line-height: 17px;
	.customer-mail{
		width:30%;
		float: left;
		span{
			color: $white;
		}
	}
	.main-search{
		width:30%;
		float: left;
		position:relative;
		input{
			width: 100%;
    		border-top: none;
    		border-left: none;
    		border-right: none;
    		background: transparent;
    		outline: none;
    		border-bottom: 1px solid #eee;
    		position: relative;
		}
		.search-icon{
			position:absolute;
			right:05px;
			top:0px;
			color: $white;
		}
	}
	.user-list{
		width:40%;
		float: left;
		ul{
			text-align: right;
			li{
				color: $white;
				display: inline-block;
    			padding: 0px 30px;
    			border-right: 1px solid #eee;
    			&:last-child{
    				border-right:none;
    			}

    			.pincode{
    				padding-left:15px;
    			}
			}
		}
	}

}

header{
	padding:0px 15px;
	.logo{
		float: left;
		padding: 10px 0px;
		@media screen and (max-width: 1024px) {
			padding: 10px;
		}
	}
	.menu-list{
		float: right;
		@media screen and (max-width: 1024px) {
			border-top:1px solid #eee;
			width: 100%;
		}
		ul{
			
			li{
				display: inline-block;
				margin-right:25px;
				position:relative;
				line-height: 100px;
				cursor: pointer;
				@media screen and (max-width: 1024px) {
					display: block;
					margin-right: 0px;
					line-height: 50px;
					padding:0px 10px;
					border-bottom: 1px solid #eee;
				}

				a{
					font-family:$body-font;
					font-weight: 600;
					color: $menu-black;
					font-size: 14px;
					transition: all 0.5s ease-in;
					position:relative;

					&:hover{
						text-decoration: none;
						transition: all 0.5s ease-in;
						color: $yellow;
					}

				}
				i{
					padding-left:10px;
				}
				.custom-triangle{
					width: 0;
					height: 0;
					border: 0 solid transparent;
					border-right-width: 10px;
					border-left-width: 10px;
					border-bottom: 15px solid #fff;
					/*transform: rotate(-360deg);*/
					position:absolute;
					bottom:  20px;
					right: -5px;
					z-index: 3;
					visibility: hidden;
					opacity: 0;
				}

				ul.custom-dropdown{
					position:absolute;
					min-width:250px;
					left: 50%;
					margin:0 auto;
					top: 80%;
					background-color:$white;
					padding:30px 15px;
					visibility: hidden;
					opacity: 0;
					box-shadow:-1px -1px 10px -2px rgba(0, 0, 0, 0.2);
					transform: translateX(-50%);
					z-index: 2;
					transition: all 0.5s ease;
					@media screen and (max-width: 1024px) {
						position:relative;
						margin-bottom: 10px;
						display: none;
					}
					li.custom-dropdown-list{
						display: block;
						line-height: 20px;
						padding:10px 0px;
						position:relative;

						a{
							&:after{
							    content: '';
							    opacity: 0;
							    position: absolute;
							    height: 2px;
							    width: 80%;
							    background: linear-gradient(90deg, rgba(255,210,55,0.9668242296918768) 35%, rgba(255,245,209,1) 100%);
							    margin: 0 auto;
							    top: 50%;
							    left: 110%;
							    transition: all 0.3s ease-in;

							
						    }
						    &:hover:after {
								opacity: 1;
								transition: all 0.3s ease-in;
							}

						}

					
					}


				}

			 &:hover{
			 	.custom-dropdown{
			 		visibility: visible;
					transition: all 0.5s ease;
					opacity: 1;
					/*@media screen and (max-width: 1024px) {
						
						opacity: 0;
						visibility: hidden;
					}*/
				}
				.custom-triangle{
					visibility: visible;
					opacity: 1;
				}
			 }
			 &:first-child{
			 	ul.custom-dropdown{
					left: 0;
					transform: none;

			 		}
				}

			}
		}

		@media screen and (max-width: 1024px) {
				display: none;
			}
	}
}

@media screen and (max-width: 1024px) {
  .mobile-menu {
    display: block;
    float: right;
    padding: 35px 10px;
    margin-right: 15px;
    font-size: 30px;
  }

}

@media screen and (min-width: 1024px) {
  .custom-hide{
  	display: none;
  }

}