/*product list section start here*/
.product-listmain{
	padding:60px 0px;
	background-color:$light-bg;
	&.left{
		background-color:$white;
	}
	.container-fluid{
		padding:0px 50px;
		.product-heading{
			width: 40%;
			float: right;
			position:relative;
			&.left-list{
				float: left;
			}
			.heading-img{
				width: 70%;
			    float: right;
			    display: block;
			    &.left-heading-img{
			    	float: left;
			    }
			    
			    img{
			    	max-width: 100%;
			    }
			}

			.product-overimg{
				position: absolute;
			    left: 10%;;
			    width: 70%;
			    top: 50%;
			    transform: translateY(-50%);
	    		display: block;
				img{
					max-width: 100%;
				}
				.product-title{
				position:absolute;
				left: 90%;
				top: 50%;
			    transform: translateY(-50%);
			    transform: rotate(90deg);
			    &.left-title{
			    	left: -25%;
			    	top: 50%;
			    	transform: translateY(-50%);
				    transform: rotate(-90deg);
			    }
				span{
					font-family: $heading-font-cond-bold;
					font-weight: 600;
					font-size:28px;
					color: $white;
					letter-spacing: 7px;

				}

			}
			}
			
		}
		.product-list{
			width: 60%;
			float: left;
			position:relative;
			padding-right:70px;
			.single-product{
				width: calc(50% - 50px);
				float: left;
				margin-right:50px;
				box-shadow:7px 4px 10px -2px rgba(0, 0, 0, 0.2);
				.product-image{
					position:relative;
					img{
						max-width: 100%;
					}
					.product-icon{
						.product-eye{
							position:absolute;
							left: 10px;
							bottom:20px;
						}
						.product-wishlist{
							position:absolute;
							right: 10px;
							bottom:20px;
						}
					}
				}
				.product-content{
					background-color:$white;
					text-align: center;
					padding: 10px 0px 0px;

					.product-title{
						font-size: 24px;
						font-family: $body-font;
						font-weight: 600;
						display: block;
					}
					.product-price{
						display: block;
						padding:10px 0px;
					}
					.custom-dropdown{
						width: 80%;
						margin:0 auto;
						background-color:#f6f6f6;
						outline: none;
						box-shadow:none;
					}
					.qauntity-product{
						padding:10px 0px;
						.sub{
							background-color:#b2b2b2;
							border:none;
							box-shadow:none;
							outline: none;
							color: $white;
							width: 20px;
							height: 20px;
						}
						.field{
							width: 20%;
	    					text-align: center;
	    					color: $black;
	    					background: #f6f6f6;
						    border: none;
						    box-shadow: none;
						    padding: 2px 0px;
						}
						.add{
							background-color:#b2b2b2;
							border:none;
							box-shadow:none;
							outline: none;
							color: $white;
							width: 20px;
							height: 20px;
						}
					}
					.product-button{
						font-family: $body-font;
						font-weight: 600;
						text-transform: capitalize;
						background-color:$product-button;
						color: $white;
						width: 100%;
					    border: none;
					    padding: 15px 0px;
					    transition: all 0.5s ease-in;

					    &:hover{
					    	background-color:$yellow;
					    	transition: all 0.5s ease-in;
					    }
					}
				}
			}
			.prduct-seemore{
				position:absolute;
				top: 50%;
				right:0px;
		  		transform: translateY(-50%);
		  		cursor: pointer;
				.see-circle{
					width: 20px;
				    height: 20px;
				    background: $yellow;
				    border-radius: 50%;
				    margin:0 auto;
				    vertical-align: middle;
				    
				  &:before {
	   					font-family: FontAwesome;
					   content: "\f178";
					   display: inline-block;
					   padding-right: 3px;
					   vertical-align: middle;
					   font-weight: 300;
					}
				}
				.see-more{
					span{
						color: $light-grey;
						font-family: $body-font;
						font-weight: 600;
					}
				}
			}
		}
	}
}


