/*seection stock with bulk start here*/
	.stock-bulk{
		background-color:$white;
		padding:60px 0px;
		position:relative;
		.stock-heading{
			width: 100%;
			display: table;
			
			.stock-left{
				display: table-cell;
				width: 50%;
				position:relative;
				vertical-align: top;
				.stock-left-heading{
					padding:30px 20px 150px;
					width: 50%;
					background-color:$yellow;
					margin-bottom: 80px;
					h6{
						font-family: $body-font;
						font-weight: 600px;
						color: $white;
						font-size: 18px;
						margin-bottom: 20px;
					}
					p{
						line-height: 20px;
						color: $white;
						text-transform: capitalize;
					}
				}
				.stock-left-image{
					position: absolute;
				    right: 10%;
				    width: 70%;
				    bottom:0;
				    
					img{
							max-width: 100%;
						}
				}
			}
			.stock-right{
				width: 50%;
				display: table-cell;
				vertical-align: top;

				h2{
					font-size: 35px;
					font-family: $heading-font;
					line-height: 50px;
