				.stock-right-body{
					p{
						line-height: 20px;
						font-family: $body-font;
					}
				}
				.prduct-seemore{
					padding:40px 0px 0px;
					.see-circle{
						width: 20px;
					    height: 20px;
					    background: $yellow;
					    border-radius: 50%;
					    margin:0 auto;
					    vertical-align: middle;
					    display: inline-block;
				    
					  &:before {
		   					font-family: FontAwesome;
						   content: "\f178";
						   display: inline-block;
						   padding-right: 3px;
						   vertical-align: middle;
						   font-weight: 300;
						}
					}
					.see-more{
						display: inline-block;
						padding-right:60px;
						span{
							color: $light-grey;
							font-family: $body-font;
							font-weight: 600;
						}
					}
				}
				.stock-right-img{
					position:absolute;
					right: 0;
					bottom:0px;
					text-align: right;
