.custom-footer{
	padding: 60px 0px;
	.footer-links{
		.single-footer{
			width: 25%;
    		float: left;
    		text-align: center;
			.footer-quick{
				display: inline-block;
				.heading-text{
				 padding:0px 0px 10px;
				 font-family: $heading-font-cond-bold;
				 font-size: 20px;
				 text-align: left;
				}
				.simple-link{
					padding:10px 0px;
					font-family: $body-font;
					font-weight: 600;
					font-size: 16px;
					text-align: left;
				}
			}
		}
	}
	
}

	.tiny-footer{
		background-color:$black;
		padding:20px 0px;
		.tiny-footer-text{
			text-align:center;
			span{
				font-size: 18px;
				font-family: $body-font;
				color: $white;
			}
		}
	}	